import React, { useMemo } from 'react';

import { AVATAR_COLORS_FILLED, AVATAR_COLORS_SUBTLE } from './AvatarColors';

import * as ST from './styled';

export enum AvatarSize {
  MD = 'md',
  LG = 'lg',
  XL = 'xl',
  XXL = 'xxl',
  EXTRA = 'extra',
}

const getNameAvatar = (firstName: string, lastName: string, isOnlyLastName: boolean): string => {
  return isOnlyLastName ? lastName.trim()?.[0] : `${lastName.trim()?.[0]}${firstName.trim()?.[0]}`;
};

export const getRadixTransformation = (char: string, interval: number) => parseInt(char, 36) % interval;

type Props = {
  src?: string;
  alt?: string;
  className?: string;
  firstName?: string;
  lastName?: string;
  fullName?: string;
  figure?: 'circle' | 'square';
  type?: 'filled' | 'subtle';
  userId?: string;
  size?: AvatarSize;
  onClick?: () => void;
};

const Avatar: React.FC<Props> = ({
  type = 'subtle',
  figure = 'circle',
  size = AvatarSize.XL,
  src,
  alt = src,
  className,
  firstName,
  lastName,
  fullName,
  userId = 'a',
  onClick,
}) => {
  const currentName: string = useMemo(() => {
    if (fullName) {
      const splitFullName = fullName.trim().split(' ');
      if (splitFullName.length > 1) {
        const firstName = splitFullName[0];
        const lastName = splitFullName[1];
        return getNameAvatar(firstName, lastName, size !== 'xl');
      } else {
        return fullName[0];
      }
    }
    if (firstName && lastName) {
      return getNameAvatar(firstName, lastName, size !== 'xl');
    }
    if (firstName && !lastName) {
      return firstName[0];
    }
    if (!firstName && lastName) {
      return lastName[0];
    }
    return '?';
  }, [firstName, lastName, size, fullName]);

  const classNameJoined = `${size} ${figure === 'circle' ? 'circle' : 'square'} ${className}`

  if (src) {
    return (
      <ST.Wrapper className={`${size} ${className} ${figure}`}>
        <ST.Image
          className={figure === 'circle' ? 'circle' : 'square'}
          src={src}
          alt={alt}
        />
      </ST.Wrapper>
    );
  }

  return (
    <ST.Wrapper
      className={classNameJoined}
      data-testid="avatar"
      onClick={onClick}
      style={
        type === 'subtle'
          ? AVATAR_COLORS_SUBTLE[
              getRadixTransformation(userId.slice(-1), AVATAR_COLORS_SUBTLE.length)
            ]
          : AVATAR_COLORS_FILLED[
              getRadixTransformation(userId.slice(-1), AVATAR_COLORS_FILLED.length)
            ]
      }
    >
      {currentName.toUpperCase()}
    </ST.Wrapper>
  );
};

export { Avatar };
