import React from 'react';
import { ModalProps } from '@components/Modal';
import { IconSize } from '@components/Icon';
import * as ST from './styled';

type SideModalProps = ModalProps & {
  trigger?: JSX.Element | ((isOpen: boolean) => JSX.Element);
  modalHeader?: React.ReactNode;
  modalFooter?: React.ReactNode;
  classNamePrefix?: string;
  titleText?: string | React.ReactNode;
  open?: boolean;
  onClose: () => void;
  className?: string;
  contentStyle?: React.CSSProperties;
  overlayStyle?: React.CSSProperties;
  closeOnDocumentClick?: boolean;
};

const SideModal: React.FC<SideModalProps> = ({
  trigger,
  open = true,
  modalHeader,
  modalFooter,
  titleText,
  children,
  onClose,
  contentStyle,
  overlayStyle,
  classNamePrefix,
  closeOnDocumentClick,
  className,
  ...rest
}) => {
  return (
    <ST.StyledModal
      {...rest}
      trigger={trigger}
      open={open}
      onClose={onClose}
      overlayStyle={{ justifyContent: 'flex-end' }}
      closeOnDocumentClick={closeOnDocumentClick}
      classNamePrefix={classNamePrefix || 'side-modal'}
      className={className}
    >
      <ST.ModalContainer>
        {modalHeader ?? (
          <ST.Header>
            <div>{typeof titleText === 'string' ? <ST.Title>{titleText}</ST.Title> : titleText}</div>
            <div>
              <ST.CloseIcon
                type="close"
                onClick={onClose}
              />
            </div>
          </ST.Header>
        )}
        <ST.ModalContent>
          <ST.ModalContentInner>{children}</ST.ModalContentInner>
        </ST.ModalContent>
        {modalFooter && <ST.ModalBottom>{modalFooter}</ST.ModalBottom>}
      </ST.ModalContainer>
    </ST.StyledModal>
  );
};

export { SideModal };
export type { SideModalProps };
