import React, { FC } from 'react';

import * as ST from './styled';

interface IProps {
  info: React.ReactNode;
  text: string;
}

export const InfoItem: FC<IProps> = ({ info, text }) => {
  return (
    <ST.InfoContainer>
      <ST.InfoText>{text}</ST.InfoText>
      {info}
    </ST.InfoContainer>
  )
}
