import { useLocation } from 'react-router-dom';
import * as ST from './styled';
import { PrivateRouteNames } from '@src/router/routes';
import { Icon, IconTypes } from '@components/Icon/Icon';
import { useAuth } from '@providers/AuthProvider';
import { Avatar, AvatarSize, Button, ButtonVariant, ProfileModal } from '@src/components';
import { useModalControls } from '@src/hooks';

type SidebarItem = {
  link: string;
  text: string;
  icon: IconTypes;
}

const SIDEBAR_ITEMS: SidebarItem[] = [
  { link: PrivateRouteNames.USERS, text: 'Пользователи', icon: 'users' },
  { link: PrivateRouteNames.POSTS, text: 'Посты', icon: 'archive' },
  { link: PrivateRouteNames.TAGS, text: 'Теги', icon: 'grid' },
  { link: PrivateRouteNames.ROLES, text: 'Роли', icon: 'lock' },
]

export const Navbar = () => {
  const { isOpen, open, close } = useModalControls();
  const { pathname } = useLocation();
  const { user, logout } = useAuth();

  return (
    <ST.Wrapper>
      <ST.Content>
        <ST.ProfileButton onClick={open}>
          <Avatar
            firstName={user?.firstName}
            lastName={user?.lastName}
            userId={user?.id}
            src={user?.avatar}
            size={AvatarSize.XXL}
          />
          <ST.ProfileTextContainer>
            <ST.ProfileText>{user?.lastName} {user?.firstName}</ST.ProfileText>
            <ST.ProfileTextEmail>{user?.email}</ST.ProfileTextEmail>
          </ST.ProfileTextContainer>
        </ST.ProfileButton>
        <ST.Links>
          {SIDEBAR_ITEMS.map(it => (
            <ST.LinkItem key={it.link} to={it.link} isActive={pathname === it.link}>
              <Icon type={it.icon} />
              {it.text}
            </ST.LinkItem>
          ))}
        </ST.Links>
      </ST.Content>

      <Button text="Выйти" fullWidth variant={ButtonVariant.DANGER} onClick={logout} />
      {isOpen && <ProfileModal onClose={close} />}
    </ST.Wrapper>
  );
}
