import styled, { css } from 'styled-components';
import { COLORS } from '@src/utils/colors';

interface Props {
  order: 'left' | 'right'
}

const getOrders = ({order}: Props) => css`
  --cb-order: ${order === 'right' ? 2 : 1};
  --label-order: ${order === 'right' ? 1 : 2};
`

export const Container = styled.div<Props>`
  ${getOrders};
  
  display: flex;
  align-items: center;
  grid-gap: 2rem;
  font-size: 3rem;
  color: ${COLORS.blue70};
  width: 100%;
  cursor: pointer;

  &.right {
    justify-content: flex-end;
  }
`;

export const Label = styled.div`
  order: var(--label-order);
`;

export const Cb = styled.div`
  order: var(--cb-order);
`;
