import { FC, PropsWithChildren, useEffect, useState } from 'react';
import { AuthContext } from './AuthContext';
import { AuthApi } from '@src/api/Auth/AuthApi';
import { REFRESH_TOKEN_KEY, TOKEN_KEY } from '@src/utils/storage';
import { IUser } from '@src/api/Users/models';
import { toastError } from '@src/utils/toast';
import { UsersApi } from '@src/api/Users/UsersApi';
import { instance } from '@src/api/base';

export const AuthProvider: FC<PropsWithChildren> = ({ children }) => {
  const [isLoading, setIsLoading] = useState(true);
  const [isAuth, setIsAuth] = useState(false);
  const [user, setUser] = useState<IUser>();

  useEffect(() => {
    const token = localStorage.getItem(TOKEN_KEY);
    const refreshToken = localStorage.getItem(REFRESH_TOKEN_KEY);

    instance.interceptors.response.use(
      (res) => {
        return res;
      },
      async (err) => {
        const originalConfig = err.config;

        if (originalConfig.url !== "/auth/login" && err.response) {
          if (err.response.status === 401 && !originalConfig._retry) {
            originalConfig._retry = true;

            try {
              await refresh();

              return instance(originalConfig);
            } catch (_error) {
              return Promise.reject(_error);
            }
          }
        }

        return Promise.reject(err);
      }
    );

    if (token || refreshToken) {
      loadUser();
    } else {
      setIsLoading(false);
    }
  }, []);

  const setAvatar = (avatar: string) => {
    setUser(prev => {
      if (prev) {
        return ({ ...prev, avatar });
      }
    });
  }

  const refresh = async () => {
    try {
      const refreshToken = localStorage.getItem(REFRESH_TOKEN_KEY);
      if (refreshToken) {
        const res = await AuthApi.refresh(refreshToken);
        localStorage.setItem(TOKEN_KEY, res.data.accessToken);
        localStorage.setItem(REFRESH_TOKEN_KEY, res.data.refreshToken);
        const userRes = await UsersApi.fetchMe();

        setUser(userRes.data);
        setIsAuth(true);
      } else {
        localStorage.removeItem(REFRESH_TOKEN_KEY);
      }
    } catch (e) {
      localStorage.removeItem(REFRESH_TOKEN_KEY);
    } finally {
      setIsLoading(false);
    }
  }

  const getMe = async () => {
    try {
      const res = await UsersApi.fetchMe();

      setUser(res.data);
    } catch (e) {
      toastError(e);
    }
  }

  const loadUser = async () => {
    try {
      setIsLoading(true);
      const res = await UsersApi.fetchMe();

      setUser(res.data);
      setIsAuth(true);
      setIsLoading(false);
    } catch (e: any) {
      localStorage.removeItem(TOKEN_KEY);
    }
  }

  const login = async (email: string, password: string) => {
    try {
      const res = await AuthApi.login(email, password);
      localStorage.setItem(TOKEN_KEY, res.data.accessToken);
      localStorage.setItem(REFRESH_TOKEN_KEY, res.data.refreshToken);
      await loadUser();
      setIsAuth(true);
    } catch (e) {
      toastError(e)
    }
  }

  const logout = () => {
    setIsAuth(false);
    localStorage.removeItem(TOKEN_KEY);
    localStorage.removeItem(REFRESH_TOKEN_KEY);
  }

  return (
    <AuthContext.Provider value={{
      isAuth,
      user,
      login,
      logout,
      isLoading,
      getMe,
      setAvatar,
    }}>
      {children}
    </AuthContext.Provider>
  )
}
