import React, { useEffect, useMemo, useState } from 'react';
import { SelectOption, useSearch } from '@components/Select/hooks/useSearch';
import { renderHeader } from '@components/Select/Header';
import { renderFooter } from '@components/Select/Footer';
import { Dropdown } from '@components/Select/Dropdown';
import { Options } from '@components/Select/Options';
import { Selection } from '@components/Select/Selection';
import * as ST from './styled';

type CmSelectSize = 'xs' | 'xl' | 'xxl' | 'xxxl';
type CmSelectValue = SelectOption<unknown> | SelectOption<unknown>[] | undefined;

type Components = {
  selectedValue: (selected: CmSelectValue) => React.ReactNode;
  optionContainer: (option: SelectOption<unknown>, selected: boolean) => React.ReactNode;
};
type Props = {
  portal?: boolean;
  searchable?: boolean;
  className?: string;
  multi?: boolean;
  selectAllAlign?: 'left' | 'right';
  disabled?: boolean;
  size?: CmSelectSize;
  placeholder?: string;
  withoutCheckmark?: boolean;
  caption?: string;
  description?: string;
  noOptionsText?: string;
  loading?: boolean;
  error?: boolean;
  autoClearValue?: boolean;
  errorText?: string;
  maxMenuHeight?: number;
  selectDescription?: string;
  replaceLabel?: boolean;
  value?: CmSelectValue;
  onChange?: (values: CmSelectValue, event?: React.SyntheticEvent) => void;
  options: SelectOption<unknown>[];
  components?: Partial<Components>;
};

const Select: React.FC<Props> = ({
  portal = false,
  size = 'lg',
  value,
  onChange = () => {},
  className,
  placeholder,
  options,
  noOptionsText,
  multi,
  searchable,
  components,
  caption,
  description,
  selectDescription,
  error,
  errorText,
  disabled,
  selectAllAlign = 'left',
  maxMenuHeight = 225,
  loading,
  withoutCheckmark,
  autoClearValue,
  replaceLabel = true,
}) => {
  const [refSelection, setRefSelection] = useState<HTMLDivElement | null>(null);

  const [open, setOpen] = useState<boolean>(false);

  const { filteredOptions, ...searchProps } = useSearch(Boolean(searchable), options);

  const selectAllType = useMemo(() => {
    if (!Array.isArray(value)) {
      return false;
    }

    if (value.length === 0) {
      return false;
    }
    return options.length === value.length ? true : 'mixed';
  }, [multi, value, options]);

  const onOpen = () => setOpen(true);
  const onClose = () => setOpen(false);

  const isSelected = (option: SelectOption<unknown>) => {
    if (!value) {
      return false;
    }

    if (Array.isArray(value)) {
      return value.some((item) => item.value === option.value);
    }

    return option.value === (value as SelectOption<unknown>).value;
  };

  const onSelectAll = () => {
    if (selectAllType === 'mixed' || selectAllType === false) {
      onChange([...options]);
    } else {
      onChange([]);
    }
  };

  const allOptions = useMemo(() => {
    return options.reduce((res, option) => {
      return [...res, option];
    }, [] as SelectOption<unknown>[]);
  }, [options]);

  useEffect(() => {
    if (autoClearValue) {
      if (Array.isArray(value)) {
        const founded = allOptions.filter((option) =>
          value.some((selectedOption) => option.value === selectedOption.value),
        );
        const values = value.map((it) => it.value);

        if (founded.length !== value.length || !founded.every((it) => values.includes(it.value))) {
          onChange(founded);
        }
      } else {
        const founded = allOptions.find((option) => value?.value === option.value);

        if (founded?.value !== value?.value) {
          onChange(founded);
        }
      }
    }
  }, [allOptions, autoClearValue]);

  const selectedValue = useMemo(() => {
    if (replaceLabel && value) {
      if (Array.isArray(value)) {
        return value.map((it) => ({
          ...it,
          label: allOptions.find((option) => it.value === option.value)?.label || it.label,
        }));
      } else {
        const foundedValue = allOptions.find((it) => it.value === value.value);
        if (foundedValue) {
          return { ...value, label: foundedValue.label };
        }
      }
    }

    return value;
  }, [allOptions, value, replaceLabel]);

  const renderedDropdown = useMemo(() => {
    if (refSelection) {
      return (
        <Dropdown
          portal={portal}
          maxMenuHeight={maxMenuHeight}
          selectionContainer={refSelection}
          onClose={onClose}
          topContent={renderHeader({
            ...searchProps,
            multi,
            selectAllAlign,
            searchable,
            selectAllType,
            optionsCount: filteredOptions.length,
            onSelectAll,
          })}
          bottomContent={renderFooter({
            selectDescription,
          })}
        >
          <Options
            options={filteredOptions}
            isSelected={isSelected}
            onChange={onChange}
            onClose={onClose}
            value={value}
            multi={multi}
            withoutCheckmark={withoutCheckmark}
          />
        </Dropdown>
      );
    }

    return null;
  }, [
    portal,
    maxMenuHeight,
    filteredOptions,
    selectDescription,
    components,
    noOptionsText,
    multi,
    searchable,
    refSelection,
    searchProps,
    selectAllType,
    selectAllAlign,
    withoutCheckmark,
  ]);

  return (
    <ST.Container className={`${className} ${size}`} disabled={disabled}>
      {caption && <ST.Caption>{caption}</ST.Caption>}
      <Selection
        ref={setRefSelection}
        loading={loading}
        value={selectedValue as CmSelectValue}
        error={error}
        disabled={disabled}
        placeholder={placeholder}
        component={components?.selectedValue}
        size={size as CmSelectSize}
        onClick={disabled ? undefined : onOpen}
      >
        {open && renderedDropdown}
      </Selection>
      {((error && errorText) || description) && (
        <ST.BottomContent>
          {error && errorText && <ST.ErrorText>{errorText}</ST.ErrorText>}
          {description && <ST.Description>{description}</ST.Description>}
        </ST.BottomContent>
      )}
    </ST.Container>
  );
};

export { Select };
export type {
  CmSelectSize,
  CmSelectValue,
  Components as CmSelectComponents,
  Props as CmSelectProps,
};
