import styled from 'styled-components';

export const Wrapper = styled.div`
  font-size: 10px;
  line-height: 16px;
  font-weight: 600;

  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  overflow: hidden;
  
  &.md {
    min-width: 16px;
    width: 16px;
    height: 16px;
  }

  &.lg {
    min-width: 18px;
    width: 18px;
    height: 18px;
  }

  &.xl {
    min-width: 24px;
    width: 24px;
    height: 24px;
  }

  &.xxl {
    min-width: 32px;
    width: 32px;
    height: 32px;
  }
  
  &.extra {
    min-width: 200px;
    width: 200px;
    height: 200px;
    font-size: 30px;
  }

  &.circle {
    border-radius: 50%;
  }

  &.square {
    border-radius: 1.5rem;
  }
`;

export const Image = styled.img`
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
`;
