import { FC } from 'react';
import * as ST from './styled';
import { Button } from '@src/components';

interface IProps {
  title: string;
  buttonText?: string;
  onButtonClick?: () => void;
}

export const PageHeader: FC<IProps> = ({ title, onButtonClick, buttonText }) => {
  return (
    <ST.Wrapper>
      <ST.Title>{title}</ST.Title>
      {buttonText && (
        <Button text={buttonText} onClick={onButtonClick} />
      )}
    </ST.Wrapper>
  );
}
