import styled, { css } from 'styled-components';
import { COLORS } from '@src/utils/colors';
import { CheckboxSize } from '@components/Checkbox/index';

interface WrapperProps {
  checked?: boolean;
  isError?: boolean;
  containerReverse?: boolean;
  disabled?: boolean;
  size?: CheckboxSize | number;
}

const getCheckboxHoverColors = ({ checked, isError }: WrapperProps) => {
  if (isError) {
    return css`
      --checkbox-bg: ${COLORS.red60};
      --border-width: 2px;
    `;
  }

  if (checked) {
    return css`
      --checkbox-bg: ${COLORS.blue70};
      --checkbox-bc: ${COLORS.blue70};
    `;
  }

  return css`
    background-color: ${COLORS.lightGray20};
    border-color: ${COLORS.gray70};
  `;
}

const getCheckboxActiveColors = ({ checked, isError }: WrapperProps) => {
  if (isError) {
    return css`
      --checkbox-bg: ${COLORS.red60};
      --border-width: 2px;
    `;
  }

  if (checked) {
    return css`
      --checkbox-bg: ${COLORS.blue80};
      --checkbox-bc: ${COLORS.blue80};
    `;
  }

  return css`
    background-color: ${COLORS.lightGray30};
    border-color: ${COLORS.gray80};
  `;
}

const getCheckboxSize = ({ size }: WrapperProps) => {
  if (size === CheckboxSize.SM) {
    return css`
      --cb-size: 12px;
    `;
  }

  if (size === CheckboxSize.MD) {
    return css`
      --cb-size: 16px;
    `;
  }

  if (size === CheckboxSize.LG) {
    return css`
      --cb-size: 20px;
    `;
  }

  return css`
    --cb-size: ${size}px;
  `;
}

export const CheckBox = styled.div<WrapperProps>`
  display: flex;
  background-color: var(--checkbox-bg);
  border-radius: 4px;
  color: ${COLORS.white};
  flex-shrink: 0;
  border: var(--border-width) solid var(--checkbox-bc);
  
  ${getCheckboxSize};
  
  width: var(--cb-size);
  height: var(--cb-size);
`;

export const Wrapper = styled.div<WrapperProps>`
  --checkbox-bg: ${COLORS.lightGray10};
  --checkbox-bc: ${COLORS.gray60};
  --border-width: 1px;
  
  display: flex;
  align-items: center;
  cursor: pointer;
  gap: 10px;
  user-select: none;
  
  ${({ containerReverse }) => containerReverse && css`
    justify-content: space-between;
    flex-direction: row-reverse;
  `}

  &:hover {
    ${CheckBox} {
      ${getCheckboxHoverColors}
    }
  }

  &:active {
    ${CheckBox} {
      ${getCheckboxActiveColors}
    }
  }
  
  ${({ disabled, checked }) => disabled && css`
    pointer-events: none;
    ${CheckBox} {
      opacity: 0.5;
      --checkbox-bc: ${COLORS.gray60};
      ${checked && css`
        --checkbox-bg: ${COLORS.lightGray50};
        --checkbox-bc: ${COLORS.lightGray50};
      `}
    }
  `}

  ${({ checked }) => checked && css`
    ${CheckBox} {
      --checkbox-bc: ${COLORS.blue70};
      --checkbox-bg: ${COLORS.blue70};
      
      ${Checkmark} {
        opacity: 1;
      }
    }
  `}
`;

export const Input = styled.input`
  display: none;
`

export const TextContainer = styled.div`
  flex-grow: 1;
  min-width: 0;
`;

export const Checkmark = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin: calc(-1 * var(--border-width));
  border-collapse: collapse;
  opacity: 0;
  width: var(--cb-size);
  height: var(--cb-size);
`;

export const Caption = styled.div<WrapperProps>`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: -0.006em;
  color: ${COLORS.darkGray140};
  ${({ disabled }) => disabled && css`
    opacity: 0.5;
  `}
  margin: 0;
`;

export const Error = styled.div`
  color: ${COLORS.red70};
  font-size: 12px;
  line-height: 16px;
  margin: 0;
`;

