import React, { SyntheticEvent } from 'react';

import { ReactComponent as CheckmarkL } from '@src/assets/icons/CheckmarkL.svg';
import { ReactComponent as CheckmarkM } from '@src/assets/icons/CheckmarkM.svg';
import { ReactComponent as CheckmarkS } from '@src/assets/icons/CheckmarkS.svg';
import { ReactComponent as MixedL } from '@src/assets/icons/MixedL.svg';
import { ReactComponent as MixedM } from '@src/assets/icons/MixedM.svg';
import { ReactComponent as MixedS } from '@src/assets/icons/MixedS.svg';

import * as ST from './styled';

type CheckboxType = 'mark' | 'mixed';

type CheckboxProps = {
  errorText?: string;
  isError?: boolean;
  containerReverse?: boolean;
  type?: CheckboxType;
  size?: CheckboxSize;
};

export enum CheckboxSize {
  LG = 'lg',
  MD = 'md',
  SM = 'sm',
}

type Props = Omit<React.ComponentPropsWithRef<'input'>, keyof CheckboxProps> & CheckboxProps;

const CHECKMARK_ICON_DICTIONARY: Record<CheckboxSize | number, Record<CheckboxType, JSX.Element>> = {
  lg: {
    mark: <CheckmarkL />,
    mixed: <MixedL />,
  },
  md: {
    mark: <CheckmarkM />,
    mixed: <MixedM />,
  },
  sm: {
    mark: <CheckmarkS />,
    mixed: <MixedS />,
  },
};

const Checkbox: React.FC<Props> = ({
  isError,
  errorText,
  checked,
  disabled,
  children,
  containerReverse,
  onClick,
  size = CheckboxSize.LG,
  type = 'mark',
  ...restProps
}) => {
  //INFO: нужно для того чтобы не было двойного всплытия с компонента
  const onInputClick = (e: SyntheticEvent) => e.stopPropagation();
  return (
    <ST.Wrapper
      disabled={disabled}
      isError={isError}
      checked={checked}
    >
      <ST.CheckBox size={size}>
        <ST.Checkmark>{CHECKMARK_ICON_DICTIONARY[size][type]}</ST.Checkmark>
        <ST.Input
          type="checkbox"
          disabled={disabled}
          checked={checked}
          onClick={onInputClick}
          {...restProps}
        />
      </ST.CheckBox>

      {children && (
        <ST.TextContainer>
          <ST.Caption disabled={disabled}>{children}</ST.Caption>
          {isError && <ST.Error>{errorText}</ST.Error>}
        </ST.TextContainer>
      )}
    </ST.Wrapper>
  );
};

export { Checkbox };
