import React, { FC, Suspense, useEffect, useMemo } from 'react';
import { Route, Routes, useLocation, useNavigate } from 'react-router-dom';

import { AppLoader, Layout } from '@src/components';

import { privateRoutes, publicRoutes, PrivateRouteNames, PublicRouteNames } from './routes';
import { useAuth } from '@providers/AuthProvider/AuthContext';

const AppRouter: FC = () => {
  const { isAuth, isLoading } = useAuth();
  const { pathname } = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    if (!isLoading) {
      if (isAuth && !Object.values(PrivateRouteNames).includes(pathname as PrivateRouteNames)) {
        navigate(PrivateRouteNames.USERS);
      } else if (!isAuth && pathname !== PublicRouteNames.LOGIN) {
        navigate(PublicRouteNames.LOGIN)
      }
    }
  }, [isAuth, isLoading]);

  const renderedRoutes = useMemo(() => {
    return isAuth ? (
      <Layout>
        <Routes>
          {privateRoutes.map(route => (
            <Route
              key={route.path}
              {...route}
            />
          ))}
        </Routes>
      </Layout>
    ) : (
      <Routes>
        {publicRoutes.map(route => (
          <Route
            key={route.path}
            {...route}
          />
        ))}
      </Routes>
    )
  }, [isAuth]);

  if (isLoading) {
    return <AppLoader />;
  }

  return (
    <Suspense fallback={<AppLoader />}>
      {renderedRoutes}
    </Suspense>
    );
};

export { AppRouter };
