import { FC } from 'react';
import { UserStatus } from '@src/api/Users/models';

import * as ST from './styled';

interface IProps {
  className?: string;
  status: UserStatus;
}

export const Status: FC<IProps> = ({ status, className }) => {
  return (
    <ST.Wrapper status={status} className={className}>
      {status}
    </ST.Wrapper>
  );
}
