import styled from 'styled-components';
import { COLORS } from '@src/utils/colors';

export const Container = styled.div`
  z-index: 1000;
  opacity: 0;
  position: absolute;
  left: 0;
  top: 35px;
  width: 100%;
`;

export const Inner = styled.div`
  border: 1px solid ${COLORS.lightGray10};
  box-shadow: 0px 7px 15px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  width: 100%;
  height: 100%;
  background: ${COLORS.white};
`;

export const Top = styled.div`
  padding-top: 16px;
`;

export const Content = styled.div`
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding: 12px 12px 8px;

  &::-webkit-scrollbar {
    width: 0;
  }
`;

export const Bottom = styled.div`
  background: ${COLORS.lightGray10};
  border-top: 1px solid ${COLORS.lightGray30};
  padding: 12px 20px;
  color: ${COLORS.gray80};
  margin-top: 12px;
`;
