import { instance } from '@src/api/base';
import { IAuthResponse } from '@src/api/Auth/models';

export class AuthApi {
  static login(email: string, password: string) {
    return instance.post<IAuthResponse>('/admin/login', { email, password })
  }

  static refresh(refreshToken: string) {
    return instance.post<IAuthResponse>('/auth/refresh', { refreshToken });
  }
}
