import { FC, useEffect, useRef, useState } from 'react';
import * as ST from './styled';
import { useAuth } from '@providers/AuthProvider';
import { AppLoader, Avatar, AvatarSize, InfoItem, Role, SideModal } from '@src/components';
import { toastError, toastSuccess } from '@src/utils/toast';
import { IconSize } from '@components/Icon';
import { UsersApi } from '@src/api/Users/UsersApi';

interface IProps {
  onClose: () => void;
}

export const ProfileModal: FC<IProps> = ({ onClose }) => {
  const [isLoading, setIsLoading] = useState(true);
  const { getMe, user, setAvatar } = useAuth();
  const inputRef = useRef<HTMLInputElement | null>(null);

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const file = e.target.files[0];

      if (!file) {
        return;
      }

      const formData = new FormData();
      formData.append('photo', file);

      try {
        const response = await UsersApi.uploadAvatar(formData);
        setAvatar(response.data.url);
        toastSuccess(response.data.message)
      } catch (error) {
        console.error(error);
      }
    }
  };

  const handleUpload = () => {
    inputRef.current?.click();
  };

  useEffect(() => {
    loadUser();
  }, []);

  const loadUser = async () => {
    try {
      setIsLoading(true);
      await getMe()
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <SideModal
      titleText="Профиль"
      onClose={onClose}
    >
      <ST.Wrapper>
        {isLoading ? (
          <AppLoader/>
        ) : (
          <>
            <ST.HeaderInfo>
              <ST.AvatarContainer onClick={handleUpload}>
                <input
                  ref={inputRef}
                  type="file"
                  onChange={handleFileChange}
                  accept="image/png, image/gif, image/jpeg"
                  hidden
                />
                <Avatar
                  firstName={user?.firstName}
                  lastName={user?.lastName}
                  src={user?.avatar}
                  userId={user?.id}
                  size={AvatarSize.EXTRA}
                  figure={'square'}
                />
                <ST.PlusIcon
                  type="plus"
                  size={IconSize.XXXL}
                />
              </ST.AvatarContainer>
              <ST.Name>{user?.lastName} {user?.firstName}</ST.Name>
            </ST.HeaderInfo>
            <InfoItem
              text="Роли:"
              info={(
                <ST.Roles>
                  {user?.roles.map(it => (
                    <Role key={it.id} role={it.value} />
                  ))}
                </ST.Roles>
              )}
            />
            <InfoItem
              text="Почта:"
              info={<ST.Email>{user?.email}</ST.Email>}
            />
          </>
        )}
      </ST.Wrapper>
    </SideModal>
  )
}
