import styled from 'styled-components';
import { Icon } from '@components/Icon';
import { COLORS } from '@src/utils/colors';
import { Modal } from '@components/Modal';

export const CloseIcon = styled(Icon)`
  cursor: pointer;
  color: ${COLORS.gray60};
  transition: color 0.2s;

  &:hover {
    color: ${COLORS.darkGray150};
  }
`;

export const Title = styled.div`
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.014em;
`;

export const Header = styled.div`
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 24px 16px 24px;
  border-bottom: 1px solid ${COLORS.lightGray40};
`;

export const ModalContainer = styled.article`
  height: 100%;
  display: flex;
  flex-direction: column;
`;

export const StyledModal = styled(Modal)`
  display: flex;
  flex-direction: column;
  background: ${COLORS.white};
  box-shadow: 0px 7px 15px rgba(0, 0, 0, 0.05);
  height: 100%;
  @keyframes modal-enter {
    0% {
      opacity: 0;
      transform: translate(100rem);
    }
    50% {
      opacity: 0.5;
      transform: translate(50rem);
    }
    100% {
      opacity: 1;
      transform: translate(0);
    }
  }

  animation: modal-enter 0.2s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;
  transform: translate(100rem);
  padding: 0 !important;
  border-radius: 8px 0 0 0;
  width: 640px;
`;

export const ModalContent = styled.div`
  overflow: hidden;
  height: 100%;
`;

export const ModalContentInner = styled.div`
  scrollbar-width: none;
  overflow-y: auto;
  padding: 24px;
  height: 100%;
`;

export const ModalBottom = styled.div`
  border-top: 1px solid colors.$light-gray-40;
  padding: 24px;
`;
