import { instance } from '@src/api/base';
import { IUser, IUserCreate } from '@src/api/Users/models';

export class UsersApi {
  static fetchUsers() {
    return instance.get<IUser[]>('/admin/users');
  }

  static fetchMe() {
    return instance.get<IUser>('/users/me');
  }

  static uploadAvatar(formData: FormData) {
    return instance.post('/users/avatar', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });
  }

  static fetchCreateUser(data: IUserCreate) {
    return instance.post('/admin/users/create', data);
  }

  static banUser(id: string) {
    return instance.post(`/admin/users/ban/${id}`);
  }

  static unbanUser(id: string) {
    return instance.post(`/admin/users/unban/${id}`);
  }
}
