import React, { FC } from 'react';
import { CmSelectComponents, CmSelectValue } from '@components/Select';
import { Icon, IconSize } from '@components/Icon';
import { Avatar, TextShorter, Tooltip } from '@src/components';
import { TOOLTIP_SELECT_ID } from '@components/Select/Dropdown';
import { OptionCustomize } from '@components/Select/hooks/useSearch';
import * as ST from './styled';

type Props = {
  value: CmSelectValue;
  disabled?: boolean;
  placeholder?: string;
  component?: CmSelectComponents['selectedValue'];
};

const Content: FC<
  OptionCustomize & {
    label: string | number;
    value: unknown;
    tooltip?: boolean;
  }
> = ({ label, value, tooltip = true, avatar, icon }) => {
  return (
    <>
      {icon && (
        <ST.StyledIcon
          type={icon}
          size={IconSize.XL}
        />
      )}
      {avatar && (
        <Avatar
          fullName={avatar}
          userId={value as string}
        />
      )}
      <TextShorter
        tooltipId={TOOLTIP_SELECT_ID}
        tooltip={tooltip}
      >
        <span>{label}</span>
      </TextShorter>
    </>
  );
};

const ValueContainer: React.FC<Props> = ({ value, placeholder, component, disabled }) => {
  if (!value || (Array.isArray(value) && value.length === 0)) {
    return (
      <TextShorter>
        <ST.Placeholder>{placeholder}</ST.Placeholder>
      </TextShorter>
    );
  }
  if (component) {
    return <>{component(value)}</>;
  }
  if (Array.isArray(value)) {
    if (value.length === 0) {
      return <ST.Placeholder>{placeholder}</ST.Placeholder>;
    } else {
      const [first, ...other] = value;
      return (
        <ST.Multi disabled={disabled}>
          <ST.Content>
            <Content
              {...(first.customize || {})}
              value={first.value}
              label={first.label}
            />
            {other.length > 0 && (
              <Tooltip
                id={TOOLTIP_SELECT_ID}
                text={other.map((it) => (
                  <ST.ContentTooltip
                    key={it.value as string}
                  >
                    <Content
                      {...(it.customize || {})}
                      value={it.value}
                      label={it.label}
                      tooltip={false}
                    />
                  </ST.ContentTooltip>
                ))}
                classes={{ tooltip: ST.Counter }}
              >
                <ST.Counter>+{other.length}</ST.Counter>
              </Tooltip>
            )}
          </ST.Content>
        </ST.Multi>
      );
    }
  } else {
    return (
      <ST.Default disabled={disabled}>
        <Content
          {...(value.customize || {})}
          value={value.value}
          label={value.label}
        />
      </ST.Default>
    );
  }
};

export { ValueContainer };
