import { FC } from 'react';
import { SearchField } from '@components/Select/SearchField';
import { SelectAllCheckbox } from '@components/Select/SelectAllCheckbox';

import * as ST from './styled';

type Props = {
  optionsCount: number;
  searchValue: string;
  onChangeSearchValue: (value: string) => void;
  onSelectAll: () => void;
  isMultiLevel?: boolean;
  searchable?: boolean;
  multi?: boolean;
  selectAllType: boolean | 'mixed';
  selectAllAlign?: 'left' | 'right';
};

const Header: FC<Props> = ({
  isMultiLevel,
  multi,
  searchable,
  optionsCount,
  searchValue,
  onChangeSearchValue,
  onSelectAll,
  selectAllType,
  selectAllAlign,
}) => {
  return (
    <>
      {searchable && (
        <ST.Search>
          <SearchField
            searchValue={searchValue}
            onChangeSearchValue={onChangeSearchValue}
          />
        </ST.Search>
      )}
      {multi && optionsCount > 5 && !isMultiLevel && (
        <ST.SelectAll className={selectAllAlign}>
          <SelectAllCheckbox
            type={selectAllType}
            align={selectAllAlign}
            onClick={onSelectAll}
          />
        </ST.SelectAll>
      )}
    </>
  );
};

const renderHeader = ({
  isMultiLevel,
  multi,
  searchable,
  selectAllAlign,
  onSelectAll,
  selectAllType,
  optionsCount,
  searchValue,
  onChangeSearchValue,
}: Props) => {
  if (isMultiLevel || (!searchable && !(multi && optionsCount > 5))) {
    return null;
  }

  return (
    <Header
      searchValue={searchValue}
      onChangeSearchValue={onChangeSearchValue}
      optionsCount={optionsCount}
      selectAllType={selectAllType}
      onSelectAll={onSelectAll}
      isMultiLevel={isMultiLevel}
      searchable={searchable}
      selectAllAlign={selectAllAlign}
      multi={multi}
    />
  );
};

export { Header, renderHeader };
