import React, { forwardRef, PropsWithChildren, SyntheticEvent } from 'react';
import { CmSelectComponents, CmSelectSize, CmSelectValue } from '@components/Select';
import { ValueContainer } from '@components/Select/ValueContainer';
import { COLORS } from '@src/utils/colors';
import { ClipLoader } from 'react-spinners';
import { Icon, IconSize } from '@components/Icon';
import * as ST from './styled';

type Props = {
  size?: CmSelectSize;
  component?: CmSelectComponents['selectedValue'];
  placeholder?: string;
  error?: boolean;
  loading?: boolean;
  disabled?: boolean;
  value: CmSelectValue;
  onClick?: (event: SyntheticEvent) => void;
};

const SelectionComponent: React.FC<Props> = ({
  size = 'md',
  value,
  error,
  placeholder,
  component,
  disabled,
  loading,
  onClick,
}) => (
  <ST.Container size={size}>
    <ST.Selection
      tabIndex={1}
      error={error}
      disabled={disabled}
      onClick={onClick}
    >
      <ST.Inner>
        <ST.Value>
          <ValueContainer
            value={value}
            disabled={disabled}
            placeholder={placeholder}
            component={component}
          />
        </ST.Value>
        <ST.Icons>
          {loading && (
            <ST.Loading>
              <ClipLoader size={13} color={COLORS.gray90} />
            </ST.Loading>
          )}
          <ST.Arrow>
            <Icon
              size={IconSize.XL}
              type="chevron"
            />
          </ST.Arrow>
        </ST.Icons>
      </ST.Inner>
    </ST.Selection>
  </ST.Container>
);

const SelectionWrapper = forwardRef<HTMLDivElement, PropsWithChildren<Props>>(
  ({ children, ...props }, ref) => {
    return (
      <ST.Wrapper
        ref={ref}
      >
        <SelectionComponent {...props} />
        {children}
      </ST.Wrapper>
    );
  },
);

SelectionWrapper.displayName = 'SelectionWrapper';
const Selection = React.memo(SelectionWrapper);

Selection.displayName = 'Selection';

export { Selection };
