import { IRole } from '@src/api/Roles/models';

export enum UserStatus {
  ACTIVE = 'ACTIVE',
  BANNED = 'BANNED',
}

export interface IUser {
  email: string,
  roles: IRole[],
  id: string,
  status: UserStatus,
  avatar?: string;
  isConfirmedEmail: true,
  firstName: string,
  lastName: string
}

export interface IUserCreate {
  email: string,
  firstName: string,
  lastName: string,
  roles: string[],
  password: string
}
