import { useMemo, useState } from 'react';
import { IconTypes } from '@components/Icon';

export type OptionCustomize = {
  description?: string;
  avatar?: string;
  icon?: IconTypes;
  disabled?: boolean;
};

export type OptionCustomizeWithChildren = {
  description?: string;
  avatar?: string;
  icon?: IconTypes;
  disabled?: boolean;
};

export type SelectOption<T, M = string> = {
  label: string | number;
  value: T;
  meta?: M;
  customize?: OptionCustomizeWithChildren;
};

const useSearch = (searchable: boolean, options: SelectOption<unknown>[]) => {
  const [searchValue, setSearchValue] = useState<string>('');

  const filteredOptions = useMemo(() => {
    if (!searchable) {
      return options;
    }
    return options.filter(
      ({ label }) => label.toString().toLowerCase().indexOf(searchValue.toLowerCase()) > -1,
    );
  }, [searchValue, searchable, options]);

  return {
    searchValue,
    onChangeSearchValue: setSearchValue,
    filteredOptions,
  };
};

export { useSearch };
