import React, { FC } from 'react';
import { CmSelectValue } from '../index';
import { SelectOption } from '@components/Select/hooks/useSearch';
import { OptionContainer } from '@components/Select/OptionContainer';

import * as ST from './styled';

type Props = {
  value?: CmSelectValue;
  options: SelectOption<unknown>[];
  isSelected: (option: SelectOption<unknown>) => boolean;
  onChange: (values: CmSelectValue, event?: React.SyntheticEvent) => void;
  onClose: () => void;
  multi?: boolean;
  withoutCheckmark?: boolean;
};

const Options: FC<Props> = ({
  value,
  options,
  onChange,
  isSelected,
  multi,
  onClose,
  withoutCheckmark,
}) => {
  const onSelectMulti = (option: SelectOption<unknown>, event: React.SyntheticEvent) => {
    if (!Array.isArray(value)) {
      onChange([option], event);
      return;
    }
    const exist = value.some((item) => item.value === option.value);

    let selected = [...value];

    if (exist) {
      selected = selected.filter((item) => item.value !== option.value);
    } else {
      selected.push(option);
    }

    onChange(selected, event);
  };

  const onSelect = (option: SelectOption<unknown>) => (event: React.SyntheticEvent) => {
    if (multi) {
      onSelectMulti(option, event);
    } else {
      onChange(option, event);
      onClose();
    }
  };

  return (
    <ST.Options>
      {options.map((option) => {
        return (
          <OptionContainer
            key={option.value as string}
            option={option}
            selectedValue={value}
            onSelectMulti={onSelectMulti}
            onChange={onChange}
            isSelected={isSelected}
            onClose={onClose}
            multi={multi}
            withoutCheckmark={withoutCheckmark}
          />
        );
      })}
      {options.length === 0 && (
        <ST.NoOptions>
          Нет опций
        </ST.NoOptions>
      )}
    </ST.Options>
  );
};

export { Options };
