import React, { FC, useMemo } from 'react';
import { CmSelectValue } from '@components/Select';
import { SelectOption } from '@components/Select/hooks/useSearch';
import { Icon, IconSize } from '@components/Icon';
import { Checkbox, CheckboxSize } from '@components/Checkbox';
import { Avatar, TextShorter } from '@src/components';
import { TOOLTIP_SELECT_ID } from '@components/Select/Dropdown';

import * as ST from './styled';

export enum CheckPosition {
  RIGHT = 'right',
  LEFT = 'left',
}

type Props = {
  selectedValue?: CmSelectValue;
  option: SelectOption<unknown>;
  isSelected: (option: SelectOption<unknown>) => boolean;
  onChange: (values: CmSelectValue, event?: React.SyntheticEvent) => void;
  onSelectMulti: (option: SelectOption<unknown>, event: React.SyntheticEvent) => void;
  multi?: boolean;
  onClose: () => void;
  withoutCheckmark?: boolean;
};

const OptionContainer: FC<Props> = ({
  option,
  multi,
  isSelected,
  onChange,
  onSelectMulti,
  onClose,
  withoutCheckmark,
}) => {
  const selected = isSelected(option);
  const { value, label } = option;
  const { avatar, icon, disabled } = useMemo(() => {
    if (option.customize) {
      return option.customize;
    }
    return {};
  }, [option]);

  const checkPosition = useMemo(() => {
    if (multi) {
      return icon || avatar ? CheckPosition.RIGHT : CheckPosition.LEFT;
    }

    return CheckPosition.RIGHT;
  }, [multi, icon, avatar]);

  const onSelect = (option: SelectOption<unknown>) => (event: React.SyntheticEvent) => {
    if (disabled) {
      return;
    }

    if (multi) {
      onSelectMulti(option, event);
    } else {
      onChange(option, event);
      onClose();
    }
  };

  const renderedCheck = useMemo(() => {
    if (multi) {
      return (
        <ST.Cb>
          <Checkbox
            size={CheckboxSize.MD}
            checked={selected}
          />
        </ST.Cb>
      );
    }

    return selected ? (
      <ST.CheckIcon
        type="check"
        size={IconSize.XL}
      />
    ) : null;
  }, [selected, multi]);

  return (
    <ST.OptionsItem
      disabled={disabled}
      onClick={onSelect(option)}
    >
      <ST.Container>
        <ST.Content
          withoutCheckmark={
              withoutCheckmark ||
              (!multi && !selected && checkPosition === CheckPosition.RIGHT)
          }
        >
          {checkPosition === CheckPosition.LEFT && renderedCheck}
          {icon && (
            <ST.StyledIcon
              type={icon}
              size={IconSize.XL}
            />
          )}
          {avatar && (
            <Avatar
              fullName={avatar}
              userId={value as string}
            />
          )}
          <TextShorter
            tooltipId={TOOLTIP_SELECT_ID}
            tooltip
          >
            {label}
          </TextShorter>
        </ST.Content>

        {checkPosition === CheckPosition.RIGHT && !withoutCheckmark && renderedCheck}
      </ST.Container>
    </ST.OptionsItem>
  );
};

export { OptionContainer };
