import React from 'react';

import { Checkbox, CheckboxSize } from '@components/Checkbox';

import * as ST from './styled';

type Props = {
  type: boolean | 'mixed';
  align?: 'left' | 'right';
  onClick: () => void;
};

const SelectAllCheckbox: React.FC<Props> = ({ type, align = 'left', onClick }) => {
  return (
    <ST.Container
      order={align}
      className={align}
      onClick={onClick}
    >
      <ST.Cb>
        <Checkbox
          size={CheckboxSize.MD}
          checked={type === true || type === 'mixed'}
          type={typeof type === 'boolean' ? 'mark' : 'mixed'}
        />
      </ST.Cb>
      <ST.Label>
        {type === true ? 'Сбросить все' : 'Выделить все'}
      </ST.Label>
    </ST.Container>
  );
};

export { SelectAllCheckbox };
