import { FC } from 'react';
import * as ST from './styled'

type Props = {
  isMultiLevel?: boolean;
  selectDescription?: string;
};

const Footer: FC<Props> = ({ selectDescription }) => {
  return (
    <>
      {selectDescription && <ST.Description>{selectDescription}</ST.Description>}
    </>
  );
};

const renderFooter = ({ isMultiLevel, selectDescription }: Props) => {
  if (isMultiLevel || (!selectDescription)) {
    return undefined;
  }

  return (
    <Footer
      isMultiLevel={isMultiLevel}
      selectDescription={selectDescription}
    />
  );
};

export { Footer, renderFooter };
